<template lang="pug">
    div(class="page")
        Header
        section(class='apply')
            div(class='container')
                div(class='title')
                    h2 {{ $t("apply") }} 
                        span {{ $t("job") }} 
                div(class='apply-content')
                    div(class='alert bg-danger alert-warning alert-dismissible fade show' role='alert' v-if="notSent")
                        p You should check in on some of those fields below.
                        //- button(type='button' class='close' data-dismiss='alert' aria-label='Close')
                            span(aria-hidden='true') &times;
                    div(class='form-area')
                        form(@submit="submitForm" ref="apply_form" enctype="multipart/form-data")
                            .row
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder="$t('full-name')" class='form-control' type='text' name="name" v-model="applyFormData.name" required)
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder="$t('phone-number')" class='form-control' type='tel' name="phone" v-model="applyFormData.phone" required)
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder="$t('Email')" class='form-control' type='email' name="email" v-model="applyFormData.email")
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder="$t('years-of-experience')" class='form-control' type='number' name="experience" v-model="applyFormData.experience")
                                div(class="col-lg-12")
                                    div(class='upload')
                                        fa(class="theicon" :icon="['fas', 'upload']")
                                        //- label.sr-only(for='file') Upload CV
                                        div(class="custom-file")
                                            input(type="file" class="file-input" id="file" ref="file" v-on:change="handleFileUpload()")
                                            p(class="file-name" v-if="this.applyFormData.file.length < 1") {{ $t("upload-file") }}
                                            p(class="file-name" v-else="this.applyFormData.file.length >= 1") {{ this.applyFormData.file.name }}
                                div(class="col-lg-12")
                                    div(class='form-group button')
                                        button(class='btn hvr-sweep-to-bottom') {{ $t("apply") }} 
                                div(class="col-lg-12" v-if="sent")
                                    p.successfully {{ successMessage }}
        Footer
</template>

<script>
// @ is an alias to /src
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import Header from "@/components/global/Header.vue";
import Footer from "@/components/global/Footer.vue";

Vue.use(VueAxios, axios);

export default {
    name: "ApplyPage",
    metaInfo() {
        return {
            title: process.env.VUE_APP_TITLE,
            titleTemplate: `%s | ${this.BlogsData.website_title}`,
            meta: [
                { charset: "utf-8" },
                {
                    name: "description",
                    content: this.BlogsData.meta_description,
                },
                {
                    name: "keywords",
                    content: this.BlogsData.meta_keywords,
                },
            ],
        };
    },
    components: {
        Header,
        Footer,
    },
    data() {
        return {
            successMessage: "",
            sent: false,
            notSent: false,
            applyFormData: {
                name: "",
                phone: "",
                email: "",
                experience: "",
                file: "",
            },
        };
    },
    methods: {
        submitForm(e) {
            e.preventDefault();
            let formData = new FormData();
            formData.append("file", this.applyFormData.file);
            formData.append("name", this.applyFormData.name);
            formData.append("phone", this.applyFormData.phone);
            formData.append("email", this.applyFormData.email);
            formData.append("experience", this.applyFormData.experience);
            axios
                .post("apply-job", formData, {
                    headers: {
                        "Content-Type": "multipart/form-data",
                    },
                })
                .then((result) => {
                    this.successMessage = result.data.data.message;
                    this.sent = true;
                    this.notSent = false;
                    // console.warn(result);
                    console.warn(this.sent);
                    this.applyFormData.name = "";
                    this.applyFormData.phone = "";
                    this.applyFormData.email = "";
                    this.applyFormData.experience = "";
                    this.applyFormData.file = "";
                    this.$refs.apply_form.reset();
                })
                .catch((error) => {
                    this.notSent = true;
                    this.sent = false;
                    console.log(error);
                });
        },
        handleFileUpload() {
            this.applyFormData.file = this.$refs.file.files[0];
        },
    },
};
</script>
